.payment {
	width: 210px;
	margin: 80px auto 24px;

	input {
		display: block;
		width: 200px;
		padding: 8px;
		margin-top: 8px;
	}

	select {
		display: block;
		/*width: 200px;
		padding: 8px;
		margin-top: 8px;
		*/
		margin-bottom: 16px;
		width: 224px;
		font-size: 18px;
		margin-top: 16px;
	}

	input[type=submit] {
		position: relative;
		width: 200px;
		top: -4px;
		display: inline-block;
		margin: 32px 0 0;
		padding: 8px 16px;
		left: 10px;
		bottom: 10px;
		background: #def9d2;
		cursor: pointer;
		border-radius: 4px;
		border: 1px solid #ccc;
		font-size: 12px;
		transition: color 0.2s, background 0.2s;
		text-decoration: none;

		&:hover {
			background-color: #bfd6b4;
		}
	}

	&-total {
		font-size: 24px;
	}

	&-form {
		.ng-valid[required], .ng-valid.required {
			border-left: 5px solid #42A948; /* green */
		}
		.ng-invalid:not(form) {
			border-left: 5px solid #a94442; /* red */
		}
	}

	&-done {
		width: 600px;
		margin: 80px auto 24px;
		font-size: 24px;
		text-align: center;
	}
}